import React, { useState, useContext, useEffect } from "react";
import { toast } from "react-toastify";
import { confirmpost, Pin } from "../../../../../api/api";
import Context from "../../../../utils/Context";
import {
  convertPersianToEnglish,
  fixnumbers,
} from "../../../../utils/functions";

export default function ConfirmCard({
  confirmCodeRef,
  onCardInputFocus,
  onCardInputBlur,
  children,
  trace,
  reLogin,
  merchent,
  transaction,
  switchHash,
}) {
  const [confirmationCode, setConfirmationCode] = useState("");

  const context = useContext(Context);
  const {
    isLoading,
    setStep,
    setModalSetting,
    setSwitchContext,
    switchContext,
  } = context;

  const handleFormChange = (event) => {
    let { name, value } = event.target;
    value = convertPersianToEnglish(value);
    setConfirmationCode(value);
  };

  useEffect(() => {
    confirmCodeRef?.current?.focus();
  }, []);

  const confirmHandler = () => {
    if (!window.navigator.onLine) {
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا از اتصال دستگاه به اینترنت اطمینان حاصل فرمایید",
        buttonText: "متوجه شدم",
      });
    } else {
      isLoading(true);
      confirmpost({
        verification_code: fixnumbers(confirmationCode),
        unique_id: trace,
      })
        .then((res) => {
          Pin({
            merchent_id: merchent,
            transaction_uuid: transaction,
            unique_id: trace,
          })
            .then((res) => {
              isLoading(false);
              setStep("pay");
              toast.success("رمز پویا ارسال شد");
            })
            .catch((err) => {
              isLoading(false);
              if (err.code === "ECONNABORTED") {
                setModalSetting({
                  title: "خطا",
                  show: true,
                  description:
                    "مشکلی در ارتباط با سرور پیش آمده لطفا دوباره تلاش کنید",
                  buttonText: "متوجه شدم",
                });
              } else {
                if (err.response) {
                  // Request made and server responded
                  if (err.response.data.can_reset === true) {
                    let available = err.response.data.meta.available.filter(
                      (x1) =>
                        !err.response.data.meta.tried.some(
                          (x2) => x1.val === x2.val
                        )
                    );
                    debugger;
                    setSwitchContext({
                      ...switchContext,
                      available: available,
                      recommended: err.response.data.meta.next,
                      failed: err.response.data.meta.tried,
                      selectedSwitch: err.response.data.meta.next,
                    });
                    reLogin();
                  } else {
                    setModalSetting({
                      title: "خطا",
                      show: true,
                      description: err.response.data.message,
                      buttonText: "متوجه شدم",
                    });
                  }
                }
              }
            });
        })
        .catch((err) => {
          isLoading(false);
          if (err.code === "ECONNABORTED") {
            setModalSetting({
              title: "خطا",
              show: true,
              description:
                "مشکلی در ارتباط با سرور پیش آمده لطفا دوباره تلاش کنید",
              buttonText: "متوجه شدم",
            });
          } else {
            if (err.response) {
              if (err.response.data.can_reset === true) {
                let available = err.response.data.meta.available.filter(
                  (x1) =>
                    !err.response.data.meta.tried.some(
                      (x2) => x1.val === x2.val
                    )
                );
                debugger;
                setSwitchContext({
                  ...switchContext,
                  available: available,
                  recommended: err.response.data.meta.next,
                  failed: err.response.data.meta.tried,
                  selectedSwitch: err.response.data.meta.next,
                });
                reLogin();
              } else {
                setModalSetting({
                  title: "خطا",
                  show: true,
                  description: err.response.data.message,
                  buttonText: "متوجه شدم",
                });
              }
            }
          }
        });
    }
  };

  return (
    <div className="card-form">
      <div className="card-list">{children}</div>
      <div className="card-form__inner">
        <div className="card-input">
          <label
            htmlFor="cardNumber"
            className="card-input__label"
            style={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            کد تایید ارسال شده
          </label>
          <input
            type="tel"
            name="confirmCode"
            className="card-input__input"
            autoComplete="off"
            onChange={handleFormChange}
            maxLength="19"
            ref={confirmCodeRef}
            onFocus={(e) => onCardInputFocus(e, "confirmCode")}
            onBlur={onCardInputBlur}
            value={confirmationCode}
          />
        </div>

        <div className="card-input">
          <input
            type="button"
            className="card-form__button"
            value="تایید"
            onClick={confirmHandler}
          />
        </div>
      </div>
    </div>
  );
}
