import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import "../../assets/styles/radioButton.scss";

export const MyRadioButton = ({ onChange, value }) => {
  const [checked, isChecked] = useState(false);

  const _handleChange = (event) => {
    onChange(!checked);
    isChecked(!checked);
  };

  useEffect(() => {
    isChecked(value);
  }, [value]);

  return (
    <span onClick={_handleChange}>
      <label for="r1" />
      <input
        type="radio"
        id="r1"
        name="r-group"
        checked={checked}
        className="my-radio-button"
      />
    </span>
  );
};
