import React, { useCallback, useContext, useState } from "react";
import { useRef } from "react";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { Images } from "../../../../../common";
import Context from "../../../../utils/Context";
import ConfirmCard from "./ConfirmCard";
import Login from "./Login";
import NationalCode from "./NationalCode";
import OtpForm from "./Otp";
import Pay from "./Pay";

export default function CForm({
  confirmCodeRef,
  cardMonth,
  cardYear,
  gateway,
  onUpdateState,
  cardNumberRef,
  phoneNumberRef,
  nationalCodeRef,
  otpRef,
  cardDateRef,
  passwordRef,
  onCardInputFocus,
  onCardInputBlur,
  cardCvv,
  children,
  cardNumber,
  phoneNumber,
  merchent,
  transaction,
  amount,
  trace,
  cbUrl,
  switchHash,
}) {
  const [loginAgain, setLoginAgain] = useState(false);
  const [resendCode, setResendCode] = useState(false);

  const context = useContext(Context);
  const { step, setStep, setReset } = context;

  let formButtonsRefObj = {
    login: useRef(),
    otp: useRef(),
    pay: useRef(),
    nationalCode: useRef(),
  };

  let focusFormButtonByKey = useCallback((key) => {
    formButtonsRefObj[key].current.focus();
  });

  const reLogin = (bool = true) => {
    if (bool) {
      setReset(true);
      setStep("login");
    }
    setLoginAgain(bool);
  };

  return (
    <div className="card-form">
      <div className="card-list">{children}</div>
      {step === "login" ? (
        <Login
          gateway={gateway}
          switchHash={switchHash}
          resendCode={resendCode}
          setResendCode={setResendCode}
          parentCardNumber={cardNumber}
          parentPhoneNumber={phoneNumber}
          reLogin={reLogin}
          loginAgain={loginAgain}
          merchent={merchent}
          transaction={transaction}
          amount={amount}
          focusOnButton={focusFormButtonByKey}
          onUpdateState={onUpdateState}
          cardNumberRef={cardNumberRef}
          phoneNumberRef={phoneNumberRef}
          onCardInputFocus={onCardInputFocus}
          onCardInputBlur={onCardInputBlur}
        />
      ) : step === "otp" ? (
        <OtpForm
          switchHash={switchHash}
          setResendCode={setResendCode}
          phoneNumber={phoneNumber}
          cardNumber={cardNumber}
          amount={amount}
          onCardInputFocus={onCardInputFocus}
          otpRef={otpRef}
          focusOnButton={focusFormButtonByKey}
          reLogin={reLogin}
        />
      ) : step === "nationalCode" ? (
        <NationalCode
          switchHash={switchHash}
          nationalCodeRef={nationalCodeRef}
          onUpdateState={onUpdateState}
          amount={amount}
          focusOnButton={focusFormButtonByKey}
        />
      ) : step === "confirmCard" ? (
        <ConfirmCard
          switchHash={switchHash}
          confirmCodeRef={confirmCodeRef}
          onCardInputFocus={onCardInputFocus}
          onCardInputBlur={onCardInputBlur}
          trace={trace}
          reLogin={reLogin}
          merchent={merchent}
          transaction={transaction}
        />
      ) : (
        <Pay
          reLogin={reLogin}
          switchHash={switchHash}
          phoneNumber={phoneNumber}
          cardNumber={cardNumber}
          focusOnButton={focusFormButtonByKey}
          merchent={merchent}
          transaction={transaction}
          amount={amount}
          cardCvv={cardCvv}
          cardDateRef={cardDateRef}
          passwordRef={passwordRef}
          confirmCodeRef={confirmCodeRef}
          cardYear={cardYear}
          onCardInputFocus={onCardInputFocus}
          onUpdateState={onUpdateState}
          cardMonth={cardMonth}
          cbUrl={cbUrl}
        />
      )}
      {/* <div className="copyright">
        Copyright 2021 JibGate secure payments gateway all rights reserved.
      </div>

      <div className="footer">
        <img
          alt="jibGate Logo"
          src={Images.jibGateLogo}
          style={{ height: 28, width: "auto" }}
        />

        <img
          alt="SSL"
          src={Images.sslFooter}
          className="sslIcon"
          style={{ height: 28, width: "auto" }}
        />
      </div> */}
    </div>
  );
}
