import React, { useState, useContext, useEffect } from "react";
import { inquiry, Otp } from "../../../../../api/api";
import { parseAuths } from "../../../../../utils/auth";
import Context from "../../../../utils/Context";
import { convertPersianToEnglish } from "../../../../utils/functions";

export default function OtpForm({
  otpRef,
  onCardInputFocus,
  onCardInputBlur,
  children,
  cardNumber,
  reLogin,
  switchHash,
  setResendCode,
  phoneNumber,
}) {
  const [otp, setOtp] = useState("");
  const [timeOut, setTimeOut] = useState(false);
  const [timeLeft, setTimeLeft] = useState({ minute: 2, second: 0 });

  const context = useContext(Context);
  const {
    isLoading,
    setStep,
    setModalSetting,
    switchContext,
    setSwitchContext,
  } = context;

  useEffect(() => {
    otpRef?.current?.focus();
  }, []);

  React.useEffect(() => {
    let myInterval = setInterval(() => {
      if (timeLeft.second > 0) {
        setTimeLeft({
          minute: timeLeft.minute,
          second: timeLeft.second - 1,
        });
      } else {
        if (timeLeft.minute === 0) {
          setTimeOut(true);
          clearInterval(myInterval);
          setTimeLeft({
            minute: 0,
            second: 0,
          });
        } else {
          setTimeLeft({
            minute: timeLeft.minute - 1,
            second: 59,
          });
        }
      }
    }, 1000);
    return () => {
      clearInterval(myInterval);
    };
  }, [timeOut, timeLeft]);

  const handleFormChange = (event) => {
    let { name, value } = event.target;
    value = convertPersianToEnglish(value);
    setOtp(value);
  };

  const _handleResend = () => {
    setTimeOut(false);
    setTimeLeft({ minute: 2, second: 0 });
    // onClickResend();
  };

  const setupotp = () => {
    if (otp.length === 0) {
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا کد ارسال شده را وارد کنید",
        buttonText: "متوجه شدم",
      });
    } else {
      isLoading(true);
      // debugger;
      Otp({
        verification_code: otp,
      })
        .then((res) => {
          isLoading(false);
          parseAuths(res.data.data.payment, "add");
          setStep("pay");
          if (res.data.data.next === "profile_complete") {
            setStep("nationalCode");
          } else {
            inquiry(
              {
                card_number: cardNumber.replace(/ /g, ""),
              },

              res.data.data.payment.uuid_access,
              res.data.data.payment.uuid_deid
            )
              .then((res) => {
                // setName(res.data.data.name)
              })
              .catch((err) => {
                if (err.response) {
                  // Request made and server responded
                  if (err.response.data.can_reset === true) {
                    let available = err.response.data.meta.available.filter(
                      (x1) =>
                        !err.response.data.meta.tried.some(
                          (x2) => x1.val === x2.val
                        )
                    );
                    debugger;
                    setSwitchContext({
                      ...switchContext,
                      available: available,
                      recommended: err.response.data.meta.next,
                      failed: err.response.data.meta.tried,
                      selectedSwitch: err.response.data.meta.next,
                    });
                    reLogin();
                  }
                }
              });
          }
        })
        .catch((err) => {
          isLoading(false);
          if (err.response) {
            // Request made and server responded
            if (err.response.data.can_reset === true) {
              let available = err.response.data.meta.available.filter(
                (x1) =>
                  !err.response.data.meta.tried.some((x2) => x1.val === x2.val)
              );
              debugger;
              setSwitchContext({
                ...switchContext,
                available: available,
                recommended: err.response.data.meta.next,
                failed: err.response.data.meta.tried,
                selectedSwitch: err.response.data.meta.next,
              });
              reLogin();
            } else {
              setModalSetting({
                title: "خطا",
                show: true,
                description: err.response.data.message,
                buttonText: "متوجه شدم",
              });
            }
          }
        });
    }
  };

  const _handleNext = () => {
    setupotp();
  };

  return (
    <div className="card-form">
      <div className="card-list">{children}</div>
      <div className="card-form__inner">
        <div className="card-input">
          <div
            dir="rtl"
            className="card-info__input"
            style={{ display: "flex", justifyContent: "space-between" }}
          >
            <span>
              {" "}
              <span className="otp-info__info">کد تایید به شماره </span>
              <span className="otp-info__number">
                {phoneNumber.replace(/ /g, "")}
              </span>{" "}
              <span className="otp-info__info"> ارسال شد</span>
            </span>

            <input
              onClick={() => setStep("login")}
              type="button"
              value="تغییر شماره"
              className="card-info__change"
            />
          </div>

          <label
            htmlFor="cardNumber"
            className="card-input__label"
            style={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            کد تایید اپلیکیشن
          </label>
          <input
            type="tel"
            name="cardNumber"
            className="card-input__input"
            autoComplete="off"
            onChange={handleFormChange}
            maxLength="19"
            ref={otpRef}
            onFocus={(e) => onCardInputFocus(e, "otp")}
            onBlur={onCardInputBlur}
            value={otp}
          />
        </div>

        <div dir="rtl">
          <span
            className="otp-info__info"
            style={{ fontSize: 16, marginLeft: 12 }}
          >
            کد را دریافت نکرده اید؟
          </span>
          <span className="otp-info__number">
            {timeLeft.minute !== 0 ? (
              <span>
                <span>
                  {timeLeft.minute / 10 > 0
                    ? parseInt(timeLeft.minute / 10) == 0
                      ? ""
                      : parseInt(timeLeft.minute / 10)
                    : ""}
                </span>
                <span>
                  {timeLeft.minute / 10 > 0
                    ? parseInt(timeLeft.minute % 10)
                    : timeLeft.minute}{" "}
                </span>
                <span dir="rtl"> دقیقه</span> و{" "}
              </span>
            ) : null}
            <span>
              {timeLeft.second / 10 > 0
                ? parseInt(timeLeft.second / 10) == 0
                  ? ""
                  : parseInt(timeLeft.second / 10)
                : ""}
              {/* {":"} */}

              <span>
                {timeLeft.second / 10 > 0
                  ? parseInt(timeLeft.second % 10)
                  : timeLeft.second}
              </span>
              <span dir="rtl"> ثانیه </span>
            </span>
          </span>
          <span className="otp-info__number">
            دیگر میتوانید مجددا درخواست ارسال کد کنید
          </span>
        </div>
        <div className="card-input">
          <div className="dynamic-password">
            <div
              // style={{ minWidth: 100 }}
              className="card-form__button-container"
            >
              <input
                type="button"
                className="card-form__button"
                value="مرحله بعد"
                onClick={_handleNext}
              />
            </div>
            <div
              // style={{ minWidth: 100 }}
              className="card-form__button-container"
            >
              <input
                disabled={!timeOut}
                type="button"
                className="card-form__send_again"
                value="ارسال مجدد کد"
                onClick={() => setResendCode(true)}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
