/* eslint-disable no-unused-vars */
import axios from "axios";
import axiosRetry, { exponentialDelay } from "axios-retry";
import {
  confirm,
  finalized,
  getcburl,
  getinfocart,
  getpin,
  initial,
  nationalCode,
  prepair,
  shaparakFinalUrl,
  shaparakFirstUrl,
  test,
  verifyotp,
} from "../constans/api";

var BASE_URL =  window.location.host;
// window.location.host;
const instance = axios.create({
  baseURL: "https://" + BASE_URL,
  withCredentials: true
});

instance.defaults.timeout = 60000;
axiosRetry(instance, {
  retryDelay: exponentialDelay,
  retries: 3,
  shouldResetTimeout: true,
  retryCondition: (err) => {
    if (!err.response) return false;
    // @ts-ignore
    if (
      err.response?.status == 404 &&
      err.response.data.message == "خطای سیستمی, لطفا مجددا تلاش نمایید!"
    ) {
      return true;
    }
    return false;
  },
});

export const Setup = (data) => instance.post(prepair, data);

export const getcb = (merchend, transaction) =>
  instance.get(getcburl(merchend, transaction));

export const Otp = (data, identifire, deid, expire) =>
  instance.post(verifyotp, data, {
    headers: {
      IDS: sessionStorage.getItem("IDS"),
    },
  });

export const TestApi = (data) => {
  instance.post(test, data, {
    headers: {
      IDS: sessionStorage.getItem("IDS"),
    },
  });
};

