import { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import { CDN_PATH } from '../../../constans/cdn';
import Layout from '../../UI/Layout/index';
import './style.css';

const Index = (props) => {
  const [status, setStatus] = useState();
  const [amount, setAmount] = useState('');
  const [transaction, setTransaction] = useState('');
  const history = useHistory();
  function useQuery() {
    return new URLSearchParams(useLocation().search);
  }
  let query = useQuery();
  useEffect(() => {
    if (status) {
      if (!query.get('amount')) {
        var _decode = atob(query.get('cb'));
        history.push(`/pay-status/false?cb=${_decode}`);
      }
    }
    if (query.get('amount')) {
      setAmount(query.get('amount'));
      setTransaction(query.get('transaction'));
    }
    setTimeout(() => {
      var _decode = atob(query.get('cb'));
      window.location = _decode;
    }, 3000);
  }, [query]);
  useEffect(() => {
    const query = () => {
      if (props.match.params.status === 'true') {
        return true;
      } else if (props.match.params.status === 'false') {
        return false;
      } else {
        return 'error';
      }
    };
    setStatus(query);
  }, [props.match.params]);
  return (
    <Layout Iswhite>
      <div className="main-success">
        <div className="top-box">
          <img
            className="img_success"
            src={`${
              status === true
                ? CDN_PATH + 'images/success.svg'
                : status === 'error'
                ? CDN_PATH + 'images/danger.svg'
                : CDN_PATH + 'images/not-success.svg'
            }`}
            alt="success"
          />
          <h2
            className={`${
              status === true
                ? 'title-operation-pay-success'
                : status === 'error'
                ? 'title-operation-pay-error'
                : 'title-operation-pay-notSuccess'
            }  txt_heavy pt-3`}>
            {status === true
              ? 'پرداخت با موفقیت انجام شد'
              : status === 'error'
              ? 'خطا هنگام پرداخت'
              : '!پرداخت با خطا مواجه شد'}
          </h2>
          <p className="massage-pay txt_regular">
            {status === true
              ? 'پرداخت شما با موفقیت انجام شد و مبلغ مورد نظر از کارت شما برداشته شد'
              : status === 'error'
              ? 'متاسفانه پذیرنده مورد نظر در حال حاضر قادر به دریافت تراکنش نمیباشد. لطفا بعدا تلاش نمایید'
              : 'متاسفانه پرداخت مورد نظر منقضی یا کامل شده است لطفا مجددا از پذیرنده درخواست پرداخت نمایید'}
          </p>
          <div
            style={{
              backgroundColor: status === true ? '#41ca7f' : status === false ? 'red' : 'yellow'
            }}
            className="line-succes"></div>
        </div>
        {status === true ? (
          <>
            <p className="back-massage-pay txt_medium mb-3">...در حال برگشت به سایت پذیرنده</p>

            <div className="info-pay-box mb-4 d-flex">
              <div className="info-pay-box-left">
                <p className="txt_medium">:شماره پیگیری</p>
                <span className="txt_persian">{transaction}</span>
                <p className="txt_medium">:تاریخ و ساعت تراکنش</p>
                <span className="txt_persian">{new Date().toLocaleString('fa-IR')}</span>
              </div>

              <div className="info-pay-box-right">
                <img className="logo_factor" src={CDN_PATH + "images/pay-info-icon.svg"} alt="" />
                <div>
                  <p className="txt_heavy description-factor">مبلغ قابل پرداخت شده</p>
                  <p className="txt_persian">
                    {amount}
                    <span className="txt_bold"> ریال</span>
                  </p>
                </div>
              </div>
            </div>
          </>
        ) : (
          ''
        )}

        <div
          className={`${
            status === false
              ? 'back-button-container'
              : status === 'error'
              ? 'back-button-container'
              : ''
          }`}>
          <a
            href="#"
            className={`${
              status === true
                ? 'back-button-success'
                : status === 'error'
                ? 'back-button-error'
                : 'back-button-notSuccess'
            } txt_heavy`}>
            برگشت به سایت پذیرنده
          </a>
        </div>
      </div>
    </Layout>
  );
};

export default Index;
