import { banks } from "../../constans/cards.constant";

const persianNumbers = [
  /۰/g,
  /۱/g,
  /۲/g,
  /۳/g,
  /۴/g,
  /۵/g,
  /۶/g,
  /۷/g,
  /۸/g,
  /۹/g,
];
const arabicNumbers = [
  /٠/g,
  /١/g,
  /٢/g,
  /٣/g,
  /٤/g,
  /٥/g,
  /٦/g,
  /٧/g,
  /٨/g,
  /٩/g,
];
export const fixnumbers = (str) => {
  if (typeof str === "string") {
    for (var i = 0; i < 10; i++) {
      str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
    }
  }
  return str;
};

export function useQuery(location) {
  return new URLSearchParams(location);
}

export const findCard = (card) => {
  return banks.find((i) => i.code === card.slice(0, 6));
};

export const hex2a = (hexx) => {
  var hex = hexx.toString(); //force conversion
  var str = "";
  for (var i = 0; i < hex.length; i += 2)
    str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
  return str;
};

export const persianNumber = (str) => {
  var regex = new RegExp("^[\u06F0-\u06F90-9]+$");
  // debugger;
  return regex.test(str);
};

export const convertPersianToEnglish = (str) => {
  var persianNumbers = [
    /۰/g,
    /۱/g,
    /۲/g,
    /۳/g,
    /۴/g,
    /۵/g,
    /۶/g,
    /۷/g,
    /۸/g,
    /۹/g,
  ];
  var arabicNumbers = [
    /٠/g,
    /١/g,
    /٢/g,
    /٣/g,
    /٤/g,
    /٥/g,
    /٦/g,
    /٧/g,
    /٨/g,
    /٩/g,
  ];

  if (typeof str === "string") {
    for (var i = 0; i < 10; i++) {
      str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
    }
  }
  return str;
};

export const emailValidation = (email) => {
  var pattern = new RegExp(
    /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
  );
  if (!pattern.test(email)) {
    return false;
  }
  return true;
};

export const convertToCurrency = (num) => {
  let amount = num.toString();
  let result = "";
  if (amount.length % 3 === 0) {
    for (let i = 0; i < amount.length; i += 3) {
      result = result + amount.slice(i, i + 3) + ",";
    }
    // debugger;
    return result;
  } else {
    if (amount.length < 3) {
      // debugger;
      return amount;
    } else {
      let remenant = amount.length % 3;
      result = amount.slice(0, remenant) + ",";
      // debugger;
      for (let i = remenant; i < amount.length - remenant; i += 3) {
        if (i + 3 >= amount.length) {
          result = result + amount.slice(i);
          // debugger;
        } else {
          result = result + amount.slice(i, i + 3) + ",";
          // debugger;
        }
      }
      // debugger;
      return result;
    }
  }
};
