import React, { useEffect, useReducer, useState } from "react";
import { useContext } from "react";
import { toast } from "react-toastify";
import {
  final,
  initiate,
  Pin,
  shaparakFinalize,
  shaparakFirst,
} from "../../../../../api/api";
import { LilSpinner } from "../../../../../component/common/LilSpinner";
import { Modal } from "../../../../../component/common/Modal";
import Context from "../../../../utils/Context";
import {
  convertPersianToEnglish,
  convertToCurrency,
  fixnumbers,
} from "../../../../utils/functions";

const currentYear = new Date().getFullYear() - 622;

const monthsArr = Array.from({ length: 12 }, (x, i) => {
  const month = i + 1;
  return month <= 9 ? "0" + month : month;
});
const yearsArr = Array.from({ length: 9 }, (_x, i) => currentYear + i);

const initialState = {
  cardMonth: "",
  cardYear: "",
  cardCvv: "",
  password: "",
  confirmCode: "",
};

const initShaparak = {
  hash: undefined,
  sigma: undefined,
  captchaImg: undefined,
  captchaModal: false,
  captchaCode: undefined,
  captchaTrace: undefined,
  vCode: undefined,
  cardModal: false,
};

const shaparakReducer = (state, action) => {
  switch (action.type) {
    case "captchaTrace":
      return { ...state, captchaTrace: action.payload };
    case "captchaCode":
      return { ...state, captchaCode: action.payload };
    case "vCode":
      return { ...state, vCode: action.payload };
    case "hash":
      return { ...state, hash: action.payload };
    case "sigma":
      return { ...state, sigma: action.payload };
    case "captchaImg":
      return { ...state, captchaImg: action.payload };
    case "captchaModal":
      return { ...state, captchaModal: action.payload };
    case "cardModal":
      return { ...state, cardModal: action.payload };
    default:
      return state;
  }
};
export default function Pay({
  onUpdateState,
  amount,
  passwordRef,
  cardDateRef,
  confirmCodeRef,
  merchent,
  transaction,
  onCardInputFocus,
  onCardInputBlur,
  cardCvv,
  children,
  cardNumber,
  phoneNumber,
  cbUrl,
  switchHash,
  reLogin,
}) {
  const [shaparak, dispatch] = useReducer(shaparakReducer, initShaparak);
  const [state, setState] = useState(initialState);
  const [trace, setTrace] = useState("");
  // const [getpin, setGetpin] = useState(false);
  const [innerBody, setInnerBody] = useState(false);
  const [isInitial, setIsInitial] = useState(false);
  const [save, setSave] = useState(false);
  const [timeOut, setTimeOut] = React.useState(true);
  const [dynamicPassLoading, setDynamicPassLoading] = React.useState(false);

  const [timeLeft, setTimeLeft] = React.useState({
    minute: 2,
    second: 0,
  });

  const context = useContext(Context);
  const {
    isLoading,
    loading,
    setModalSetting,
    setStep,
    switchContext,
    setSwitchContext,
  } = context;

  useEffect(() => {
    cardCvv?.current?.focus();
  }, []);

  const redirectToClient = () => {
    isLoading(true);
    setTimeout(() => {
      isLoading(false);
      window.location = cbUrl;
    }, 3000);
  };

  useEffect(() => {
    if (timeLeft) {
      let myInterval = setInterval(() => {
        if (timeLeft.second > 0) {
          setTimeLeft({
            minute: timeLeft.minute,
            second: timeLeft.second - 1,
          });
        } else {
          if (timeLeft.minute === 0) {
            setTimeOut(true);
            clearInterval(myInterval);
            setTimeLeft({
              minute: 0,
              second: 0,
            });
          } else {
            setTimeLeft({
              minute: timeLeft.minute - 1,
              second: 59,
            });
          }
        }
      }, 1000);

      return () => {
        clearInterval(myInterval);
      };
    }
  });

  useEffect(() => {
    if (JSON.parse(localStorage.getItem("card"))) {
      let expire = JSON.parse(localStorage.getItem("expire"));
      if (expire) {
        setState({ ...state, cardMonth: expire.month, cardYear: expire.year });
      }
    }
  }, []);

  const isNetConnected = () => {
    if (!window.navigator.onLine) {
      isLoading(false);
      setDynamicPassLoading(false);
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا از اتصال دستگاه به اینترنت اطمینان حاصل فرمایید",
        buttonText: "متوجه شدم",
      });
      return false;
    }
    return true;
  };

  const handleEconAborted = (err) => {
    debugger;
    if (err.code === "ECONNABORTED") {
      setModalSetting({
        title: "خطا",
        show: true,
        description: "مشکلی در ارتباط با سرور پیش آمده لطفا دوباره تلاش کنید",
        buttonText: "متوجه شدم",
      });
    } else {
      if (err.response) {
        if (err.response.status === 409) {
          debugger;
          setIsInitial(false);
        }
        // Request made and server responded
        if (err.response.data.can_reset === true) {
          let available = err.response.data.meta.available.filter(
            (x1) =>
              !err.response.data.meta.tried.some((x2) => x1.val === x2.val)
          );

          // let available = err.response.data.meta.available.filter(
          //   (x) => !err.response.data.meta.tried.includes(x)
          // );
          debugger;
          setSwitchContext({
            ...switchContext,
            available: available,
            recommended: err.response.data.meta.next,
            failed: err.response.data.meta.tried,
            selectedSwitch: err.response.data.meta.next,
          });
          reLogin();
          setModalSetting({
            title: "توجه",
            show: true,
            description: "سوییچ بانکی عوض شد",
            buttonText: "متوجه شدم",
          });
        } else {
          setModalSetting({
            title: "خطا",
            show: true,
            description: err.response.data.message,
            buttonText: "متوجه شدم",
          });
        }
      }
    }
  };

  const finalizePay = (traceVal = trace) => {
    debugger;
    final({
      verification_code: fixnumbers(state.confirmCode),
      merchent_id: merchent,
      transaction_uuid: transaction,
      unique_id: traceVal,
      source_card: fixnumbers(cardNumber.replace(/ /g, "")),
      pin: fixnumbers(state.password),
      exp_month: fixnumbers(state.cardMonth),
      exp_year: fixnumbers(state.cardYear),
      cvv2: fixnumbers(state.cardCvv),
    })
      .then((res) => {
        localStorage.setItem("gateway", JSON.stringify(res.data.data.gateway));
        localStorage.setItem("card", JSON.stringify(cardNumber));
        localStorage.setItem("phone", JSON.stringify(phoneNumber));
        if (save) {
          localStorage.setItem(
            "expire",
            JSON.stringify({
              month: state.cardMonth,
              year: state.cardYear,
            })
          );
        }

        setModalSetting({
          title: "موفق",
          show: true,
          description:
            "تراکنش با موفقیت انجام شد. در حال بازگشت به سایت پذیرنده",
          buttonText: "متوجه شدم",
        });
        redirectToClient();
        isLoading(false);
      })
      .catch((err) => {
        isLoading(false);
        handleEconAborted(err);
      });
  };

  const sendPin = (trace) => {
    Pin({
      merchent_id: merchent,
      transaction_uuid: transaction,
      unique_id: trace,
    })
      .then((res) => {
        setDynamicPassLoading(false);
        isLoading(false);
        toast.success("رمز پویا ارسال شد");
        setTimeLeft({ minute: 2, second: 0 });
        setTimeOut(false);
      })
      .catch((err) => {
        isLoading(false);
        setDynamicPassLoading(false);
        handleEconAborted(err);
      });
  };

  const validateInputsDynamicPass = () => {
    if (fixnumbers(state.cardCvv).length < 3) {
      setDynamicPassLoading(false);
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا از صحت شماره Cvv2 وارد شده اطمینان حاصل نمایید",
        buttonText: "متوجه شدم",
      });
      return false;
    } else if (
      fixnumbers(state.cardYear).length < 1 ||
      fixnumbers(state.cardMonth).length < 1
    ) {
      setDynamicPassLoading(false);
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا از صحت شماره تاریخ انقضا اطمینان حاصل فرمایید",
        buttonText: "متوجه شدم",
      });
      return false;
    }
    return true;
  };

  const initializePay = (staticPass) => {
    debugger;
    let traceVal;
    initiate({
      merchent_id: merchent,
      transaction_uuid: transaction,
      source_card: fixnumbers(cardNumber.replace(/ /g, "")),
      pin: "123456",
      exp_month: fixnumbers(state.cardMonth),
      exp_year: fixnumbers(state.cardYear),
      cvv2: fixnumbers(state.cardCvv),
    })
      .then((res) => {
        if (res.data.data.next === "card_otp") {
          if (res.data.data.data) {
            setTrace(res.data.data.data.unique_id);
            traceVal = res.data.data.data.unique_id;
            onUpdateState("trace", res.data.data.data.unique_id);
          } else {
            traceVal = res.data.data.transaction.unique_id;

            onUpdateState("trace", res.data.data.transaction.unique_id);
            setTrace(res.data.data.transaction.trace);
          }
          setDynamicPassLoading(false);
          isLoading(false);
          setModalSetting({
            title: "توجه",
            show: true,
            description:
              "لطفا کد تاییدیه ارسال شده به شماره همراه متصل به کارت بانکی را وارد کنید",
            buttonText: "متوجه شدم",
          });

          if (res.data.data.data && res.data.data.data.type === "dialog") {
            setStep("confirmCard");
          } else {
            setInnerBody(true);
          }
          setIsInitial(true);
        } else if (res.data.data.next === "shaparak") {
          dispatch({
            type: "captchaTrace",
            payload: res.data.data.transaction.uuid,
          });
          dispatch({ type: "hash", payload: res.data.data.extra.hash });
          dispatch({ type: "sigma", payload: res.data.data.extra.sigma });
          dispatch({
            type: "captchaImg",
            payload: res.data.data.transaction.captcha,
          });
          dispatch({ type: "captchaModal", payload: true });
          isLoading(false);
        } else {
          onUpdateState("trace", res.data.data.transaction.trace);
          setTrace(res.data.data.transaction.trace);
          traceVal = res.data.data.transaction.trace;

          setIsInitial(true);
          if (staticPass) {
            finalizePay(traceVal);
          } else {
            sendPin(res.data.data.transaction.trace);
          }
        }
      })
      .catch((err) => {
        isLoading(false);
        setDynamicPassLoading(false);
        handleEconAborted(err);
      });
  };

  const dynamicHandler = () => {
    if (!isNetConnected()) {
      return;
    } else {
      if (!validateInputsDynamicPass()) {
        return;
      } else {
        setDynamicPassLoading(true);
        // isLoading(true);
        if (isInitial) {
          sendPin(trace);
        } else {
          debugger;
          initializePay();
        }
      }
    }
  };

  const finalize = () => {
    debugger;
    console.log("isInitial", isInitial);
    if (!isNetConnected()) {
      return;
    } else {
      isLoading(true);
      if (!isInitial) {
        initializePay(true);
      } else {
        finalizePay();
      }
    }
  };

  const _handlePay = () => {
    if (
      ((state.confirmCode.length > 3 && innerBody) ||
        (state.confirmCode.length === 0 && !innerBody)) &&
      state.cardMonth.length === 2 &&
      state.cardYear.length === 2 &&
      (state.cardCvv.length === 3 || state.cardCvv.length === 4) &&
      state.password.length > 3 &&
      state.confirmationCode !== 0
    ) {
      finalize();
    } else {
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا اطلاعات خواسته شده را صحیح وارد کنید",
        buttonText: "متوجه شدم",
      });
    }
  };

  const _handleGetDynamicPassword = () => {
    dynamicHandler();
  };

  const handleFormChange = (event) => {
    let { name, value } = event.target;
    value = convertPersianToEnglish(value);
    setState({ ...state, [name]: value });
    onUpdateState(name, value);
  };

  const onCvvFocus = (event) => {
    onUpdateState("isCardFlipped", true);
  };

  const onCvvBlur = (event) => {
    onUpdateState("isCardFlipped", false);
  };

  const Timer = () => {
    return (
      <span
        style={{
          display: "flex",
          flexDirection: "row",
          color: "white",
          fontWeight: "bold",
        }}
      >
        {timeLeft.second / 10 > 0
          ? parseInt(timeLeft.second % 10)
          : timeLeft.second}
        <span style={{ color: "white", fontWeight: "bold" }}>
          {timeLeft.second / 10 > 0 ? parseInt(timeLeft.second / 10) : "0"}
          {":"}
        </span>
        <span style={{ color: "white", fontWeight: "bold" }}>
          {timeLeft.minute / 10 > 0
            ? parseInt(timeLeft.minute % 10)
            : timeLeft.minute}{" "}
        </span>
        <span style={{ color: "white", fontWeight: "bold" }}>
          {timeLeft.minute / 10 > 0 ? parseInt(timeLeft.minute / 10) : 0}
        </span>
      </span>
    );
  };

  const startShaparakInit = () => {
    if (loading === true) {
      return;
    }
    isLoading(true);

    shaparakFirst({
      captcha: shaparak.captchaCode,
      transaction_uuid: shaparak.captchaTrace,
      hash: shaparak.hash,
      sigma: shaparak.sigma,
    })
      .then((res) => {
        if (res.data.status === "failed") {
          isLoading(true);
          dispatch({ type: "captchaImg", payload: res.data.captcha });
          setModalSetting(res.data.error);
        } else {
          dispatch({ type: "captchaModal", payload: false });
          dispatch({ type: "cardModal", payload: true });
          isLoading(false);
        }
      })
      .catch((err) => {
        isLoading(true);
        handleEconAborted(err);
      });
  };

  const startShaparakFinalize = () => {
    if (loading === true) {
      return;
    }
    isLoading(true);

    shaparakFinalize({
      exp_month: parseInt(state.cardMonth),
      exp_year:
        parseInt(state.cardYear) >= 90 && parseInt(state.cardYear) <= 99
          ? 1300 + parseInt(state.cardYear)
          : 1400 + parseInt(state.cardYear),
      verification_code: shaparak.vCode,
      transaction_uuid: shaparak.captchaTrace,
      hash: shaparak.hash,
      sigma: shaparak.sigma,
    })
      .then((res) => {
        setIsInitial(false);
        isLoading(false);
        setDynamicPassLoading(false)
        dispatch({ type: "cardModal", payload: false });
      })
      .catch((err) => {
        handleEconAborted(err);
      });
  };

  console.log(shaparak);
  return (
    <div className="card-form">
      <Modal
        show={shaparak.captchaModal}
        handleClose={() => dispatch({ type: "captchaModal", payload: false })}
        title={"سامانه شاپرک"}
        description={
          "طبق مصوبه بانک مرکزی تراکنش های بانک مبدا شما مستلزم تایید پیامکی از سامانه شاپرک میباشد، لطفا ابتدا کد امنیتی زیر را وارد و سپس کد پیامک دریافت شده را وارد کنید."
        }
        buttonText={"تایید کد"}
        onSubmit={startShaparakInit}
        // abort={modalSetting.abort}
      >
        <br></br>
        <img
          src={shaparak.captchaImg}
          alt="captcha_img"
          style={{ marginBottom: "10px" }}
        />
        <input
          pattern="\d*"
          type="text"
          placeholder="لطفا کد تصویری یا کپچا را وارد کنید"
          className="input_confirm txt_persian"
          style={{ marginTop: 32, marginBottom: 32 }}
          onChange={(e) =>
            dispatch({ type: "captchaCode", payload: e.target.value })
          }
        />
      </Modal>

      <Modal
        show={shaparak.cardModal}
        handleClose={() => dispatch({ type: "cardModal", payload: false })}
        title={"سامانه شاپرک"}
        description={"کد تایید پیامک شده به شماره همراه را وارد کنید، لطفا درنظر داشته باشید که درصورت اشتباه وارد کردن کد صحفه را رفرش(مجددا) باز نمایید و مراحل را انجام دهید."}
        buttonText={"تایید کد"}
        onSubmit={startShaparakFinalize}
        // abort={modalSetting.abort}
      >
        <input
          pattern="\d*"
          type="text"
          placeholder="لطفا کد تایید ۶ رقمی را وارد کنید"
          className="input_confirm txt_persian "
          style={{ marginTop: 32, marginBottom: 32 }}
          onChange={(e) => dispatch({ type: "vCode", payload: e.target.value })}
        />
      </Modal>

      <div className="card-list">{children}</div>
      <div className="card-form__inner">
        <input
          style={{ marginTop: 24 }}
          type="text"
          disabled
          className="purchase-price"
          value={`مبلغ:  ${convertToCurrency(amount)} تومان`}
        />

        {innerBody ? (
          <div className="card-input">
            <label
              htmlFor="cardNumber"
              className="card-input__label"
              style={{
                display: "flex",
                justifyContent: "flex-end",
              }}
            >
              کد تایید ارسال شده
            </label>
            <input
              dir="rtl"
              type="tel"
              name="confirmCode"
              className="card-input__input"
              autoComplete="off"
              onChange={handleFormChange}
              maxLength="19"
              ref={confirmCodeRef}
              onFocus={(e) => onCardInputFocus(e, "confirmCode")}
              onBlur={onCardInputBlur}
              value={state.confirmCode}
            />
          </div>
        ) : null}
        <div dir="rtl" className="card-input">
          <label
            dir="ltr"
            htmlFor="cardNumber"
            className="dynamic-password__label"
            style={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            رمز دوم
          </label>
          <div className="dynamic-password">
            <div className="dynamic-password__wrapper">
              <input
                type="password"
                name="password"
                pattern="\d*"
                className="dynamic-password__input"
                autoComplete="off"
                onChange={handleFormChange}
                maxLength="19"
                ref={passwordRef}
                onFocus={(e) => onCardInputFocus(e, "password")}
                onBlur={onCardInputBlur}
                value={state.password}
              />
            </div>

            <div className="dynamic-password__button-container">
              {timeOut ? (
                dynamicPassLoading ? (
                  <div className="dynamic-password__spinner">
                    <LilSpinner loading={true} />
                  </div>
                ) : (
                  <input
                    type="button"
                    className="dynamic-password__button"
                    value="دریافت رمز پویا"
                    onClick={_handleGetDynamicPassword}
                  />
                )
              ) : (
                <div className="dynamic-password__button-clicked">
                  <Timer />
                </div>
              )}
            </div>
          </div>
          <div className="dynamic-password__button-footer">
            رمز پویا را دریافت نکرده‌اید؟
          </div>
        </div>

        <div className="card-form__row">
          <div className="card-form__col">
            <div className="card-form__group">
              <label
                dir="rtl"
                htmlFor="cardMonth"
                className="card-input__label"
              >
                تاریخ انقضا
              </label>
              <select
                name="cardYear"
                className="card-input__input -select"
                value={state.cardYear}
                onChange={handleFormChange}
                onFocus={(e) => onCardInputFocus(e, "cardYear")}
                onBlur={onCardInputBlur}
              >
                <option value="" disabled>
                  سال
                </option>

                {yearsArr.map((val, index) => (
                  <option key={index} value={val.toString().slice(2)}>
                    {val.toString().slice(2)}
                  </option>
                ))}
              </select>
              <select
                className="card-input__input -select"
                value={state.cardMonth}
                name="cardMonth"
                onChange={handleFormChange}
                ref={cardDateRef}
                onFocus={(e) => onCardInputFocus(e, "cardMonth")}
                onBlur={onCardInputBlur}
              >
                <option value="">ماه</option>

                {monthsArr.map((val, index) => (
                  <option key={index} value={val}>
                    {val}
                  </option>
                ))}
              </select>
            </div>
          </div>
          <div className="card-form__col -cvv">
            <div className="card-input">
              <label dir="rtl" htmlFor="cardCvv" className="card-input__label">
                CVV2
              </label>
              <input
                dir="rtl"
                pattern="\d*"
                type="password"
                className="card-input__input"
                maxLength="4"
                autoComplete="off"
                name="cardCvv"
                value={state.cardCvv}
                onChange={handleFormChange}
                onFocus={onCvvFocus}
                onBlur={onCvvBlur}
                ref={cardCvv}
              />
            </div>
          </div>
        </div>

        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
          }}
          dir="ltr"
          className="card-input"
        >
          <label
            style={{
              margin: 0,
              paddingRight: 12,
              marginTop: 8,
              color: "#b3b3b3",
            }}
            dir="rtl"
            htmlFor="saveInfo"
            className="card-input__label"
          >
            ذخیره اطلاعات کارت جهت تراکنش های بعدی
          </label>
          <input
            className="card-form__saveInfo"
            name="saveInfo"
            type="checkbox"
            onChange={(event) => setSave(event.value)}
            value={save}
          />
        </div>

        <div className="card-input">
          <div className="dynamic-password">
            <div
              style={{ minWidth: 100 }}
              className="dynamic-password__button-container"
            >
              <input
                type="button"
                className="card-form__abort-button"
                value="انصراف"
                onClick={redirectToClient}
              />
            </div>
            <div className="dynamic-password__wrapper">
              <input
                type="button"
                className="card-form__pay-button"
                value="پرداخت"
                onClick={_handlePay}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
