import { useTimer } from 'react-timer-hook';

function IndexPin({ expiryTimestamp, expire }) {
  const { seconds, minutes } = useTimer({
    expiryTimestamp,
    // () => setGetpin(false)
    onExpire: expire
  });

  return (
    <>
      <div>{`${JSON.stringify(minutes).length === 1 ? '0' : ''}${minutes}:${
        JSON.stringify(seconds).length === 1 ? '0' : ''
      }${seconds}`}</div>
    </>
  );
}

export default IndexPin;
