import React, { useState } from "react";
import { Close } from "../../assets/icons/Close";
import { Info } from "../../assets/icons/Info";
import { Tick } from "../../assets/icons/Tick";
import "./styles.scss";

export const Modal = ({
  handleClose,
  show,
  children,
  title,
  description,
  buttonText,
  abortTransaction,
  onSubmit,
  abort,
}) => {
  const [bounce, setBounce] = useState("in");

  const _handleAnimationEnd = () => {
    if (bounce === "out") {
      handleClose();
      setBounce("in");
    }
  };

  const _handleAnimatedClosure = () => {
    setBounce("out");
    if (abort) {
      abortTransaction();
    }
    if (onSubmit) {
      onSubmit();
    }
  };

  return (
    <div className={show ? "my-modal display-block" : "my-modal display-none"}>
      <section
        dir="rtl"
        className={
          bounce === "in"
            ? "my-modal-main my-modal-main__display-block"
            : "my-modal-main my-modal-main__display-none"
        }
        onAnimationEnd={_handleAnimationEnd}
      >
        {title === "خطا" ? <Close /> : title === "موفقیت" ? <Tick /> : <Info />}
        <h3 className="my-modal-main__header">{title}</h3>

        <span className="my-modal-main__description">{description}</span>
        {children}

        <button
          type="button"
          className="my-modal-main__button"
          onClick={_handleAnimatedClosure}
        >
          {buttonText}
        </button>
      </section>
    </div>
  );
};
