import React, { useEffect, useState, useContext } from "react";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { inquiry, Setup } from "../../../../../api/api";
import { MyRadioButton } from "../../../../../component/common/MyRadioButton";
import { parseAuths } from "../../../../../utils/auth";
import Context from "../../../../utils/Context";
import {
  convertPersianToEnglish,
  convertToCurrency,
  fixnumbers,
} from "../../../../utils/functions";
import "./styles.scss";

const initialSwitchState = {
  switchAvailable: false,
  changeSwitch: false,
};

export default function Login({
  onUpdateState,
  gateway,
  switchHash,
  cardNumberRef,
  phoneNumberRef,
  onCardInputFocus,
  onCardInputBlur,
  focusOnButton,
  loginAgain,
  reLogin,
  resendCode,
  setResendCode,
  amount,
  parentCardNumber,
  parentPhoneNumber,
  merchent,
  transaction,
}) {
  const [cardNumber, setCardNumber] = useState("");
  const [maskedCardNumber, setMaskedCardNumber] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [switchState, setSwitchState] = useState(initialSwitchState);

  const context = useContext(Context);
  const {
    isLoading,
    setStep,
    protectCard,
    setProtectCard,
    setModalSetting,
    switchContext,
    setSwitchContext,
  } = context;

  // debugger;
  useEffect(() => {
    cardNumberRef?.current?.focus();
  }, []);

  useEffect(() => {
    if (loginAgain) {
      setSwitchState({ ...switchState, switchAvailable: true });
      reLogin(false);
    }
  }, [loginAgain]);

  useEffect(() => {
    if (resendCode) {
      setUp();
    }
  }, [resendCode]);

  // useEffect(() => {
  //   if (fixnumbers(phoneNumber).length > 11) {
  //     setErrorphone("length");
  //   } else {
  //     setErrorphone("true");
  //   }
  // }, [fixnumbers(phoneNumber)]);

  useEffect(() => {
    if (
      parentPhoneNumber.length > phoneNumber.length &&
      !parentPhoneNumber.includes("#")
    ) {
      let value = parentPhoneNumber
        .replace(/\D/g, "")
        .replace(/(\d{1})/g, "$1 ");
      setPhoneNumber(value.trimRight());
    }
    if (
      parentCardNumber.length > cardNumber.length &&
      !parentCardNumber.includes("#")
    ) {
      setCardNumber(parentCardNumber);
      setMaskedCardNumber(
        maskCardNumber(refactorCardNumberSpaces(parentCardNumber))
      );
    }
  }, [parentPhoneNumber, parentCardNumber]);

  const handleFormChange = (event) => {
    let { name, value } = event.target;
    value = convertPersianToEnglish(value);
    onUpdateState(name, value.split(" ").join(""));

    onPhoneNumberChange(event);
  };

  console.log(switchContext.selectedSwitch);
  const handleSwitchChange = (event) => {
    let { name, value } = event.target;
    let temp = switchContext.available.filter((x) => x.val === value);
    debugger;
    setSwitchContext({
      ...switchContext,
      selectedSwitch: temp[0],
    });
    // onUpdateState(name, value);
  };

  const refactorCardNumberSpaces = (value) => {
    let cardNumber = value;
    value = value.replace(/\D/g, "");
    if (/^3[47]\d{0,13}$/.test(value)) {
      cardNumber = value
        .replace(/(\d{4})/, "$1 ")
        .replace(/(\d{4}) (\d{6})/, "$1 $2 ");
    } else if (/^3(?:0[0-5]|[68]\d)\d{0,11}$/.test(value)) {
      // diner's club, 14 digits
      cardNumber = value
        .replace(/(\d{4})/, "$1 ")
        .replace(/(\d{4}) (\d{6})/, "$1   $2 ");
    } else if (/^\d{0,16}$/.test(value)) {
      // regular cc number, 16 digits
      cardNumber = value
        .replace(/(\d{4})/, "$1   ")
        .replace(/(\d{4})[ ]{2,}(\d{4})/, "$1   $2   ")
        .replace(/(\d{4})[ ]{2,}(\d{4})[ ]{2,}(\d{4})/, "$1   $2   $3   ");
    }
    return cardNumber.trimRight();
  };
  // TODO: We can improve the regex check with a better approach like in the card component.
  const onCardNumberChange = (event) => {
    // debugger;
    setProtectCard(false);
    let { value, name } = event.target;
    value = convertPersianToEnglish(value);
    if (name === "maskedCardNumber") {
      let temp;
      if (cardNumber.length > value.length) {
        temp = cardNumber.slice(0, -1);
      } else {
        temp = cardNumber + value.slice(-1);
      }
      // debugger;
      setCardNumber(refactorCardNumberSpaces(temp));
      onUpdateState("cardNumber", refactorCardNumberSpaces(temp));
      setMaskedCardNumber(maskCardNumber(refactorCardNumberSpaces(temp)));
    } else {
      setCardNumber(refactorCardNumberSpaces(value));
      setMaskedCardNumber(maskCardNumber(refactorCardNumberSpaces(value)));
      onUpdateState(name, refactorCardNumberSpaces(value));
    }
  };

  const maskCardNumber = (cardNumber) => {
    let cardNumberArr = cardNumber.split("   ");
    if (cardNumberArr.length === 2) {
      cardNumberArr[1] = "*".repeat(cardNumberArr[1].length);
    }
    if (cardNumberArr.length >= 3) {
      cardNumberArr[1] = "*".repeat(cardNumberArr[1].length);
      cardNumberArr[2] = "*".repeat(cardNumberArr[2].length);
    }
    return cardNumberArr.join("   ");
  };

  const onPhoneNumberChange = (event) => {
    let { value, name } = event.target;
    value = convertPersianToEnglish(value);

    let phoneNumber = value;
    value = value.replace(/\D/g, "");
    phoneNumber = value.replace(/(\d{1})/g, "$1 ");
    setPhoneNumber(phoneNumber.trimRight());
  };

  const _handleNext = () => {
    setUp();
  };

  const _handleVisibleCard = (value) => {
    setProtectCard(!value);
  };

  const switchuser = () => {
    setModalSetting({
      title: "توجه",
      show: true,
      description:
        "سوویچ بانکی شما عوض شد، لطفا مجددا فرایند پرداخت را انجام دهید.",
      buttonText: "متوجه شدم",
    });
    setUp();
  };

  const setUp = () => {
    let phoneNum = parentPhoneNumber.replace(/ /g, "");
    let cardNum = parentCardNumber.replace(/ /g, "");

    if (!window.navigator.onLine) {
      isLoading(true);
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا از اتصال دستگاه به اینترنت اطمینان حاصل فرمایید",
        buttonText: "متوجه شدم",
      });
    } else {
      debugger;
      if (resendCode) {
        isLoading(true);
        Setup({
          manual_gateway:
            switchContext.selectedSwitch?.val?.length === 0
              ? gateway
              : switchContext.selectedSwitch.val,
          merchent_id: merchent,
          transaction_uuid: transaction,
          phone_number:
            fixnumbers(phoneNum).length === 10
              ? "0" + fixnumbers(phoneNum)
              : fixnumbers(phoneNum),
          gateway_type: "shetab",
          email: "email@email.com",
          card_number: fixnumbers(cardNum),
        })
          .then((res) => {
            setResendCode(false);
            parseAuths(res.data.data.auth);
            if (res.data.data.next_step === "PREPARE") {
              isLoading(false);
              setStep("pay");
              parseAuths(res.data.data.otp, "add");
              inquiry(
                {
                  card_number: fixnumbers(cardNum),
                },
                res.data.data.auth.payment_identifier,
                res.data.data.auth.payment_deid,
                res.data.data.auth.payment_expire,
                res.data.data.otp.uuid_access,
                res.data.data.otp.uuid_deid
              )
                .then((res) => {
                  // setName(res.data.data.name);
                  isLoading(false);
                })
                .catch((err) => {
                  if (err.response) {
                    // Request made and server responded
                    if (err.response.data.can_reset === true) {
                      switchuser();
                    }
                  }
                });
            } else {
              if (res.data.data.next_step === "profile_complete") {
                parseAuths(res.data.data.otp, "add");
                isLoading(false);
                setStep("nationalCode");
              } else {
                setModalSetting({
                  title: "توجه",
                  show: true,
                  description: "کد مجدد برای شما ارسال شد",
                  buttonText: "متوجه شدم",
                });

                setStep("otp");
                isLoading(false);
              }
            }
          })
          .catch((err) => {
            isLoading(false);
            if (err.code === "ECONNABORTED") {
              setModalSetting({
                title: "خطا",
                show: true,
                description:
                  "مشکلی در ارتباط با سرور پیش آمده لطفا دوباره تلاش کنید",
                buttonText: "متوجه شدم",
              });
            } else {
              if (err.response) {
                // Request made and server responded
                if (err.response.data.can_reset === true) {
                  switchuser();
                } else {
                  setModalSetting({
                    title: "خطا",
                    show: true,
                    description: err.response.data.message,
                    buttonText: "متوجه شدم",
                  });
                }
              }
            }
          });
      } else {
        // debugger;
        if (
          (fixnumbers(phoneNum).slice(0, 2) === "09" ||
            fixnumbers(phoneNum).slice(0, 1) === "9") &&
          fixnumbers(phoneNum).length > 0 &&
          fixnumbers(phoneNum).length <= 11 &&
          fixnumbers(cardNum).length === 16
        ) {
          isLoading(true);
          debugger;
          Setup({
            manual_gateway:
              switchContext.selectedSwitch?.val?.length === 0
                ? gateway
                : switchContext.selectedSwitch.val,
            merchent_id: merchent,
            transaction_uuid: transaction,
            phone_number:
              phoneNum.length === 10
                ? "0" + fixnumbers(phoneNum)
                : fixnumbers(phoneNum),
            gateway_type: "shetab",
            email: "email@email.com",
            card_number: fixnumbers(cardNum),
          })
            .then((res) => {
              parseAuths(res.data.data.auth);
              if (res.data.data.next_step === "PREPARE") {
                setStep("pay");
                parseAuths(res.data.data.otp, "add");
                isLoading(false);
                inquiry(
                  {
                    card_number: fixnumbers(cardNum),
                  },
                  res.data.data.auth.payment_identifier,
                  res.data.data.auth.payment_deid,
                  res.data.data.auth.payment_expire,
                  res.data.data.otp.uuid_access,
                  res.data.data.otp.uuid_deid
                )
                  .then((res) => {
                    isLoading(false);
                  })
                  .catch((err) => {
                    if (err.response) {
                      // Request made and server responded
                      if (err.response.data.can_reset === true) {
                        switchuser();
                      } else {
                        isLoading(false);
                      }
                    } else {
                      isLoading(false);
                    }
                  });
              } else {
                if (res.data.data.next_step === "profile_complete") {
                  parseAuths(res.data.data.otp, "add");
                  setStep("nationalCode");
                  isLoading(false);
                } else {
                  setStep("otp");
                  isLoading(false);
                }
              }
            })
            .catch((err) => {
              isLoading(false);
              if (err.code === "ECONNABORTED") {
                setModalSetting({
                  title: "خطا",
                  show: true,
                  description:
                    "مشکلی در ارتباط با سرور پیش آمده لطفا دوباره تلاش کنید",
                  buttonText: "متوجه شدم",
                });
              } else {
                if (err.response) {
                  // Request made and server responded
                  if (err.response.data.can_reset === true) {
                    switchuser();
                  } else {
                    setModalSetting({
                      title: "خطا",
                      show: true,
                      description: err.response.data.message,
                      buttonText: "متوجه شدم",
                    });
                  }
                }
              }
            });
        } else {
          isLoading(false);
          setModalSetting({
            title: "خطا",
            show: true,
            description: "لطفا اطلاعات خواسته شده را صحیح وارد کنید",
            buttonText: "اصلاح",
          });
        }
      }
    }
  };

  return (
    <div className="card-form">
      <div className="card-form__inner">
        {switchState.switchAvailable ? (
          <div
            dir="rtl"
            className="card-switcher__info"
            style={{
              border: switchState.changeSwitch ? "none" : "1px solid",
              display: "flex",
              justifyContent: "space-between",
              // marginBottom: 24,
            }}
          >
            <span
              style={{ fontSize: 20 }}
              className={
                switchState.changeSwitch ? "display-hide" : "display-show"
              }
            >
              {" "}
              <span className="otp-info__info"> سوییچ </span>
              <span style={{ marginRight: 4 }} className="otp-info__number">
                {switchContext.failed.length > 0
                  ? switchContext.failed[0].name
                  : "Not Available"}
              </span>{" "}
            </span>

            <div
              style={{ width: "100%" }}
              className={`card-switcher ${
                switchState.changeSwitch ? " display-show" : "display-hide"
              }`}
            >
              <label
                dir="rtl"
                htmlFor="switch"
                className="card-switcher__label"
                style={{
                  display: "block",
                  // justifyContent: "flex-end",
                }}
              >
                {" "}
                سوییچ
              </label>
              <select
                name="switch"
                className="card-switcher__input -select"
                value={switchContext.selectedSwitch.val}
                onChange={handleSwitchChange}
                onFocus={(e) => onCardInputFocus(e, "switch")}
                onBlur={onCardInputBlur}
              >
                <option value={switchContext.recommended.val}>
                  {switchContext.recommended.name}
                </option>

                {/* <option value="" disabled>
                  {switchContext.failed.length > 0
                    ? switchContext.failed[0].name
                    : "Not Available"}
                </option> */}

                {switchContext.available.map((val, index) => (
                  <option key={index} value={val.val}>
                    {val.name}
                  </option>
                ))}

                {/* {switchContext.failed.map((val, index) => (
                  <option key={index} value={val.value} disabled>
                    {val.name}
                  </option>
                ))} */}
              </select>
            </div>

            <input
              onClick={() =>
                setSwitchState({ ...switchState, changeSwitch: true })
              }
              type="button"
              value="تغییر سوییچ"
              className={`card-info__change ${
                switchState.changeSwitch ? "display-hide" : "display-show"
              }`}
            />
          </div>
        ) : null}

        <div className="purchase-price">
          <span className="purchase-price__text">{`مبلغ: ${convertToCurrency(
            amount
          )} تومان`}</span>
        </div>

        <div className="card-input">
          <label
            htmlFor="cardNumber"
            className="card-input__label"
            style={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            شماره کارت
          </label>
          <div
            style={{ display: "flex", flexDirection: "row" }}
            className="card-input__input"
          >
            {!protectCard ? (
              <input
                type="text"
                pattern="\d*"
                name="cardNumber"
                style={{
                  border: "0px solid",
                  marginBottom: 0,
                  height: "100%",
                  boxShadow: "0px 0px 0px -0px rgba(32, 56, 117, 0.35)",
                }}
                className="card-input__input"
                autoComplete="off"
                onChange={onCardNumberChange}
                maxLength="25"
                ref={cardNumberRef}
                onFocus={(e) => onCardInputFocus(e, "cardNumber")}
                onBlur={onCardInputBlur}
                value={cardNumber}
              />
            ) : (
              <input
                type="tel"
                name="maskedCardNumber"
                style={{
                  border: "0px solid",
                  marginBottom: 0,
                  height: "100%",
                  boxShadow: "0px 0px 0px -0px rgba(32, 56, 117, 0.35)",
                }}
                className="card-input__input"
                autoComplete="off"
                onChange={onCardNumberChange}
                maxLength="25"
                ref={cardNumberRef}
                onFocus={(e) => onCardInputFocus(e, "cardNumber")}
                onBlur={onCardInputBlur}
                value={maskedCardNumber}
              />
            )}

            <MyRadioButton onChange={_handleVisibleCard} value={!protectCard} />
          </div>
        </div>

        <div className="card-input">
          <label
            style={{
              display: "flex",
              justifyContent: "flex-end",
            }}
            htmlFor="cardName"
            className="card-input__label"
          >
            شماره موبایل
          </label>
          <input
            placeholder="0 9 # # # # # # # # #"
            type="text"
            className="card-input__input"
            autoComplete="off"
            name="phoneNumber"
            maxLength={20}
            onChange={handleFormChange}
            value={phoneNumber}
            ref={phoneNumberRef}
            onFocus={(e) => onCardInputFocus(e, "phoneNumber")}
            onBlur={onCardInputBlur}
          />
        </div>

        <div className="card-form__row">
          <input
            type="button"
            className="card-form__button"
            value="مرحله بعد"
            onClick={_handleNext}
          />
        </div>
      </div>
    </div>
  );
}
