import React from "react";
import { useState } from "react";
import "./App.scss";

import MainScreen from "./screens/MainScreen";
import Context from "./utils/Context";

function SecondGateWay() {
  const [loading, isLoading] = useState(false);
  const [reset, setReset] = useState(false);

  const [protectCard, setProtectCard] = useState(false);
  const [modalSetting, setModalSetting] = useState({
    show: true,
    title: "توجه",
    description:
      "کاربر محترم لطفا در صورت تغییر سوییچ بانکی مراحل پرداخت را مجددا تا انتها انجام دهید",
    buttonText: "متوجه شدم",
  });

  const [switchContext, setSwitchContext] = useState({
    selectedSwitch: { name: "780", val: "" },
    available: [],
    failed: [],
    recommended: {},
  });

  const [step, setStep] = useState("login");

  return (
    <Context.Provider
      value={{
        reset: reset,
        setReset: (bool) => setReset(bool),
        loading: loading,
        isLoading: (bool) => isLoading(bool),
        step: step,
        setStep: (step) => setStep(step),
        modalSetting: modalSetting,
        setModalSetting: (setting) => setModalSetting(setting),
        protectCard: protectCard,
        setProtectCard: (bool) => setProtectCard(bool),
        switchContext: switchContext,
        setSwitchContext: (data) => setSwitchContext(data),
      }}
    >
      <MainScreen />
    </Context.Provider>
  );
}

export default SecondGateWay;
