import React, { useState, useContext, useEffect } from "react";
import { postNational } from "../../../../../api/api";
import Context from "../../../../utils/Context";
import {
  convertPersianToEnglish,
  convertToCurrency,
  fixnumbers,
} from "../../../../utils/functions";

export default function NationalCode({
  onUpdateState,
  nationalCodeRef,
  onCardInputFocus,
  onCardInputBlur,
  reLogin,
  switchHash,
  amount,
}) {
  const [nationalCode, setNationalCode] = useState("");

  const context = useContext(Context);
  const {
    isLoading,
    setStep,
    setModalSetting,
    setSwitchContext,
    switchContext,
  } = context;

  useEffect(() => {
    nationalCodeRef?.current?.focus();
  }, []);

  const handleFormChange = (event) => {
    let { name, value } = event.target;
    value = convertPersianToEnglish(value);
    setNationalCode(value);
    onUpdateState(name, value.split("").join(""));
  };

  const switchuser = () => {
    setModalSetting({
      title: "توجه",
      show: true,
      description:
        "سوویچ بانکی شما عوض شد، لطفا مجددا فرایند پرداخت را انجام دهید.",
      buttonText: "متوجه شدم",
    });

    reLogin();
  };

  const _handleNext = () => {
    if (!window.navigator.onLine) {
      isLoading(true);
      setModalSetting({
        title: "توجه",
        show: true,
        description: "لطفا از اتصال دستگاه به اینترنت اطمینان حاصل فرمایید",
        buttonText: "متوجه شدم",
      });
    } else {
      // debugger;
      isLoading(true);
      postNational({
        national_code: fixnumbers(nationalCode),
      })
        .then((res) => {
          isLoading(false);
          setStep("pay");
        })
        .catch((err) => {
          isLoading(false);
          if (err.code === "ECONNABORTED") {
            setModalSetting({
              title: "توجه",
              show: true,
              description:
                "مشکلی در ارتباط با سرور پیش آمده لطفا دوباره تلاش کنید",
              buttonText: "متوجه شدم",
            });
          } else {
            if (err.response) {
              if (err.response.data.can_reset === true) {
                let available = err.response.data.meta.available.filter(
                  (x1) =>
                    !err.response.data.meta.tried.some(
                      (x2) => x1.val === x2.val
                    )
                );
                debugger;
                setSwitchContext({
                  ...switchContext,
                  available: available,
                  recommended: err.response.data.meta.next,
                  failed: err.response.data.meta.tried,
                  selectedSwitch: err.response.data.meta.next,
                });
                switchuser();
              } else {
                setModalSetting({
                  title: "توجه",
                  show: true,
                  description: err.response.data.message,
                  buttonText: "متوجه شدم",
                });
              }
            }
          }
        });
    }
  };

  return (
    <div className="card-form">
      <div className="card-form__inner">
        <input
          type="text"
          disabled
          className="purchase-price"
          value={`مبلغ:  ${convertToCurrency(amount)} تومان`}
        />
        <div className="card-input">
          <p
            className="txt_medium"
            style={{ textAlign: "right", marginBottom: 24, marginTop: 24 }}
          >
            {" "}
            لطفا برای انجام تراکنش کد ملی مالک شماره همراه و شماره کارت را وارد
            کنید
          </p>
          <label
            htmlFor="nationalCode"
            className="card-input__label"
            style={{
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            کد ملی
          </label>

          <input
            type="tel"
            name="nationalCode"
            className="card-input__input"
            autoComplete="off"
            onChange={handleFormChange}
            maxLength="10"
            ref={nationalCodeRef}
            onFocus={(e) => onCardInputFocus(e, "nationalCode")}
            onBlur={onCardInputBlur}
            value={nationalCode}
          />
        </div>

        <div className="card-form__row">
          <input
            type="button"
            className="card-form__button"
            value="مرحله بعد"
            onClick={_handleNext}
          />
        </div>
      </div>
    </div>
  );
}
