import React, { useState } from "react";
import { useEffect } from "react";
import { useParams } from "react-router-dom";
import { toast } from "react-toastify";
import { getcb } from "./api/api";
import { Spinner } from "./component/common/Spinner";
import Main from "./component/pages/main/index";
import SecondGateWay from "./UI2";

const LayoutSwitcher = () => {
  const [gateWay, setGateWay] = useState(2);
  const [loading, isLoading] = useState(true);

  let props = useParams();

  useEffect(() => {
    if (!window.navigator.onLine) {
      toast.error("لطفا از اتصال دستگاه به اینترنت اطمینان حاصل فرمایید");
    } else {
      isLoading(true);
      getcb(props.merchent, props.transaction)
        .then((res) => {
          setGateWay(res.data.layout);
          isLoading(false);
        })
        .then((res) => {
          // history.push(`${props.match.params.merchent}/${props.match.params.transaction}/error`);
        })
        .catch((err) => {
          setGateWay(err?.data?.layout);
          isLoading(false);
        });
    }
  }, []);

  return loading ? (
    <Spinner loading />
  ) : gateWay === 1 ? (
    <Main {...props} />
  ) : (
    <SecondGateWay {...props} />
  );
};

export default LayoutSwitcher;
