import React, { useState, useEffect, useMemo, useContext } from "react";
import {
  CSSTransition,
  TransitionGroup,
  SwitchTransition,
} from "react-transition-group";
import { findCard, fixnumbers } from "../../../../utils/functions";
import "./styles.scss";
import Context from "../../../../utils/Context";
import { CDN_PATH } from "../../../../../constans/cdn";

const cardBackgroundName = () => {
  return CDN_PATH + "13.jpeg";
};

const BACKGROUND_IMG = cardBackgroundName();

const Card = ({
  phoneNumber,
  cardNumber,
  cardMonth,
  cardYear,
  cardCvv,
  isCardFlipped,
  currentFocusedElm,
  onCardElementClick,
  cardNumberRef,
  phoneNumberRef,
  cardDateRef,
}) => {
  const [style, setStyle] = useState(null);
  const [logo, setLogo] = useState(null);

  const context = useContext(Context);
  const { protectCard, setModalSetting } = context;

  const cardType = (cardNumber) => {
    if (
      fixnumbers(cardNumber.replace(/ /g, "")).length >= 6 &&
      !cardNumber.includes("#")
    ) {
      let fc = findCard(fixnumbers(cardNumber.replace(/ /g, "")));
      if (fc) {
        setLogo(fc.logo);
      } else {
        debugger;
        setModalSetting({
          show: true,
          buttonText: "اصلاح",
          title: "خطا",
          description: "کارت نامعتبر میباشد",
        });
        setTimeout(() => {
          onCardElementClick("cardNumber");
        }, 1500);
      }
    } else {
      setLogo(null);
    }
  };

  const useCardType = useMemo(() => {
    if (
      cardNumber.replace(/ /g, "").length === 16 &&
      !cardNumber.includes("#")
    ) {
      onCardElementClick("phoneNumber");
    }
    return cardType(cardNumber);
  }, [cardNumber]);

  const outlineElementStyle = (element) => {
    return element
      ? {
          width: `${element.offsetWidth}px`,
          height: `${element.offsetHeight}px`,
          transform: `translateX(${element.offsetLeft}px) translateY(${element.offsetTop}px)`,
        }
      : null;
  };

  useEffect(() => {
    if (currentFocusedElm) {
      const style = outlineElementStyle(currentFocusedElm.current);
      setStyle(style);
    }
  }, [currentFocusedElm]);

  const maskCardNumber = (cardNumber) => {
    let cardNumberArr = cardNumber.split("");
    cardNumberArr.forEach((val, index) => {
      if (index > 4 && index < 18 && protectCard) {
        if (cardNumberArr[index] !== " ") {
          cardNumberArr[index] = "*";
        }
      }
    });
    return cardNumberArr;
  };

  return (
    <div className={"card-item " + (isCardFlipped ? "-active" : "")}>
      <div className="card-item__side -front">
        <div
          className={`card-item__focus ${currentFocusedElm ? `-active` : ``}`}
          style={style}
        />
        <div className="card-item__cover">
          <img
            alt=""
            src={BACKGROUND_IMG}
            className="card-item__bg"
          />
        </div>

        <div className="card-item__wrapper">
          <div className="card-item__top">
            <img src={CDN_PATH  + "chip.png"} alt="" className="card-item__chip" />
            <div className="card-item__type">
              <TransitionGroup component="div" className="slide-fade-up">
                {logo ? (
                  <CSSTransition classNames="slide-fade-up" timeout={250}>
                    <img
                      alt={useCardType}
                      src={CDN_PATH + `images/banks/${logo}`}
                      className="card-item__typeImg"
                    />
                  </CSSTransition>
                ) : null}
              </TransitionGroup>
            </div>
          </div>

          <label
            style={{
              width: "100%",
            }}
            className="card-item__number"
            ref={cardNumberRef}
            onClick={() => onCardElementClick("cardNumber")}
          >
            <TransitionGroup
              style={{
                width: "100%",
                display: "flex",
                alignItems: "center",
              }}
              className="slide-fade-up"
              component="div"
            >
              {cardNumber && protectCard ? (
                maskCardNumber(cardNumber).map((val, index) => (
                  <CSSTransition
                    classNames="slide-fade-up"
                    timeout={250}
                    key={index}
                  >
                    <div className="card-item__numberItem">{val}</div>
                  </CSSTransition>
                ))
              ) : cardNumber && !protectCard ? (
                maskCardNumber(cardNumber).map((val, index) => (
                  <CSSTransition
                    classNames="slide-fade-up"
                    timeout={250}
                    key={index}
                  >
                    <div className="card-item__numberItem">{val}</div>
                  </CSSTransition>
                ))
              ) : (
                <CSSTransition classNames="slide-fade-up" timeout={250}>
                  <div className="card-item__numberItem">#</div>
                </CSSTransition>
              )}
            </TransitionGroup>
          </label>
          <div className="card-item__content">
            <label
              className="card-item__info"
              onClick={() => onCardElementClick("phoneNumber")}
              ref={phoneNumberRef}
            >
              <div className="card-item__holder">شماره موبایل</div>
              <div className="card-item__name">
                <TransitionGroup component="div" className="slide-fade-up">
                  {phoneNumber.includes("#") ? (
                    <CSSTransition classNames="slide-fade-up" timeout={250}>
                      <div>09#########</div>
                    </CSSTransition>
                  ) : (
                    phoneNumber.split("").map((val, index) => (
                      <CSSTransition
                        timeout={250}
                        classNames="slide-fade-right"
                        key={index}
                      >
                        <span className="card-item__nameItem">{val}</span>
                      </CSSTransition>
                    ))
                  )}
                </TransitionGroup>
              </div>
            </label>
            <div
              style={{
                width: 100,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
              className="card-item__date"
              onClick={() => onCardElementClick("cardDate")}
              ref={cardDateRef}
            >
              <label className="card-item__dateTitle">تاریخ انقضا</label>
              <label htmlFor="cardYear" className="card-item__dateItem">
                <SwitchTransition out-in>
                  <CSSTransition
                    classNames="slide-fade-up"
                    timeout={250}
                    key={cardYear}
                  >
                    <span>
                      {!cardYear ? "##" : cardYear.toString().substr(-2)}
                    </span>
                  </CSSTransition>
                </SwitchTransition>
              </label>
              /
              <label className="card-item__dateItem">
                <SwitchTransition in-out>
                  <CSSTransition
                    classNames="slide-fade-up"
                    timeout={200}
                    key={cardMonth}
                  >
                    <span>{!cardMonth ? "##" : cardMonth} </span>
                  </CSSTransition>
                </SwitchTransition>
              </label>
            </div>
          </div>
        </div>
      </div>

      <div className="card-item__side -back">
        <div className="card-item__cover">
          <img
            alt=""
            src={BACKGROUND_IMG}
            className="card-item__bg"
          />
        </div>
        <div className="card-item__band" />
        <div className="card-item__cvv">
          <div className="card-item__cvvTitle">CVV</div>
          <div className="card-item__cvvBand">
            <TransitionGroup>
              {cardCvv.split("").map((val, index) => (
                <CSSTransition
                  classNames="zoom-in-out"
                  key={index}
                  timeout={250}
                >
                  <span>*</span>
                </CSSTransition>
              ))}
            </TransitionGroup>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
