import { useHistory } from 'react-router-dom';
import { useTimer } from 'react-timer-hook';

function Index({ expiryTimestamp, time1, cb }) {
  const history = useHistory();
  const { seconds, minutes } = useTimer({
    expiryTimestamp,
    onExpire: () => history.push(`/pay-status/false?cb=${cb}`)
  });
  // console.log(JSON.stringify(minutes)[1]);

  return (
    <>
      {time1 ? (
        <div>{`${minutes}:${seconds}`}</div>
      ) : (
        <>
          <div className="txt_bold">
            {JSON.stringify(seconds).length === 1 ? '0' + seconds : seconds}
          </div>
          <span>:</span>
          <div className="txt_bold">
            {JSON.stringify(minutes).length === 1 ? '0' + minutes : minutes}
          </div>
        </>
      )}
    </>
  );
}

export default Index;
