import React, {
  useState,
  useRef,
  useCallback,
  useEffect,
  useMemo,
} from "react";
import CForm from "./components/form";
import Card from "./components/card";
import { Spinner } from "../../../component/common/Spinner";
import { useContext } from "react";
import Context from "../../utils/Context";
import { Modal } from "../../../component/common/Modal";
import { getcb } from "../../../api/api";
import { hex2a, useQuery } from "../../utils/functions";
import { useHistory, useLocation } from "react-router-dom";
import { banks } from "../../../constans/cards.constant";
import { useParams } from "react-router-dom";
import { Timer } from "./components/timer";

const initialState = {
  cardNumber: "####   ####   ####   ####",
  bankLogo: null,
  phoneNumber: "0 9 # # # # # # # #",
  cardMonth: "",
  cardYear: "",
  cardCvv: "",
  nationalCode: "",
  isCardFlipped: false,
  amount: 0,
  transaction: "",
  transactionId: "",
  merchent: null,
  cbUrl: "",
  candisable: false,
  trace: "",
  gateway: "",
};

const MainScreen = () => {
  let props = useParams();
  const [state, setState] = useState({
    ...initialState,
    merchent: props?.merchent,
    transaction: props?.transaction,
  });
  const [currentFocusedElm, setCurrentFocusedElm] = useState(null);

  const history = useHistory();

  const context = useContext(Context);
  const { isLoading, loading, modalSetting, setModalSetting, setProtectCard } =
    context;

  let query = useQuery(useLocation.search);

  const redirectToClient = () => {
    setTimeout(() => {
      // debugger;
      window.location = state.cbUrl;
    }, 3000);
  };

  useEffect(() => {
    if (!window.navigator.onLine) {
      setModalSetting({
        title: "خطا",
        show: true,
        description: "لطفا از اتصال دستگاه به اینترنت اطمینان حاصل فرمایید",
        buttonText: "متوجه شدم",
      });
    } else {
      isLoading(true);
      getcb(state.merchent, state.transaction)
        .then((res) => {
          isLoading(false);
          setState({
            ...state,
            amount: res.data.transaction.amount / 10,
            transactionId: res.data.transaction,
            cbUrl: res.data.cb_url,
          });
        })
        .then((res) => {
          // history.push(`${props.match.params.merchent}/${props.match.params.transaction}/error`);
        })
        .catch((err) => {
          isLoading(false);

          if (err.code === "ECONNABORTED") {
            setModalSetting({
              title: "خطا",
              show: true,
              description:
                "مشکلی در ارتباط با سرور پیش آمده لطفا دوباره تلاش کنید",
              buttonText: "متوجه شدم",
            });
          } else {
            if (err.response) {
              setState({
                ...state,
                cbUrl: err.response.data.data.cb_url,
              });
              setModalSetting({
                title: "خطا",
                show: true,
                description:
                  "متاسفانه پرداخت منقضی یا کامل شده است لطفا مجددا از پذیرنده درخواست پرداخت نمایید",
                buttonText: "لغو تراکنش",
                abort: true,
              });
            }
          }
        });
    }
  }, [state.merchent]);

  useEffect(() => {
    if (query.get("pt")) {
      const findCard = banks.find(
        (i) => i.code === hex2a(query.get("metrica")).slice(0, 6)
      );
      setState({
        ...state,
        candisable: true,
        phoneNumber: hex2a(query.get("pt")).substring(1),
        cardNumber: hex2a(query.get("metrica")),
      });
      if (findCard) {
        setState({
          ...state,
          bankLogo: findCard.logo,
        });
      }
    } else {
      let cachedCard = JSON.parse(localStorage.getItem("card"));
      if (cachedCard) {
        let expire = JSON.parse(localStorage.getItem("expire"));
        if (expire) {
          setState({
            ...state,
            cardNumber: cachedCard,
            phoneNumber: JSON.parse(localStorage.getItem("phone")),
            cardMonth: expire.month,
            cardYear: expire.year,
          });
        } else {
          setState({
            ...state,
            cardNumber: cachedCard,
            phoneNumber: JSON.parse(localStorage.getItem("phone")),
          });
        }
        setProtectCard(true);
      }
    }
  }, [query.get("pt"), query.get("metrica")]);

  const updateStateValues = useCallback(
    (keyName, value) => {
      setState({
        ...state,
        [keyName]: value || initialState[keyName],
      });
    },
    [state]
  );

  useMemo(() => {
    let cachedCard = JSON.parse(localStorage.getItem("card"));
    let gateway = JSON.parse(localStorage.getItem("gateway"));
    if (cachedCard) {
      let expire = JSON.parse(localStorage.getItem("expire"));
      if (expire) {
        setState({
          ...state,
          gateway: gateway,
          cardNumber: cachedCard,
          phoneNumber: JSON.parse(localStorage.getItem("phone")),
          cardMonth: expire.month,
          cardYear: expire.year,
        });
      } else {
        setState({
          ...state,
          gateway: gateway,
          cardNumber: cachedCard,
          phoneNumber: JSON.parse(localStorage.getItem("phone")),
        });
      }
      setProtectCard(true);
    }
  }, []);

  // References for the Form Inputs used to focus corresponding inputs.
  let formFieldsRefObj = {
    cardNumber: useRef(),
    phoneNumber: useRef(),
    cardDate: useRef(),
    otpRef: useRef(),
    cardCvv: useRef(),
    nationalCode: useRef(),
    passwordRef: useRef(),
    confirmCodeRef: useRef(),
    switch: useRef(),
  };

  let focusFormFieldByKey = useCallback((key) => {
    formFieldsRefObj[key]?.current?.focus();
  });

  // This are the references for the Card DIV elements.
  let cardElementsRef = {
    cardNumber: useRef(),
    phoneNumber: useRef(),
    cardDate: useRef(),
    otp: useRef(),
    nationalCode: useRef(),
    cvv2: useRef(),
    password: useRef(),
  };

  let onCardFormInputFocus = (_event, inputName) => {
    const refByName = cardElementsRef[inputName];
    setCurrentFocusedElm(refByName);
  };

  let onCardInputBlur = useCallback(() => {
    setCurrentFocusedElm(null);
  }, []);

  const _handleAbortTransaction = (event) => {
    if (state.cbUrl.length > 0) {
      window.location = state.cbUrl;
    } else {
      history.goBack();
    }
  };

  const _handleTimeout = () => {
    setModalSetting({
      title: "خطا",
      show: true,
      description:
        "متاسفانه پرداخت منقضی یا کامل شده است لطفا مجددا از پذیرنده درخواست پرداخت نمایید",
      buttonText: "لغو تراکنش",
      abort: true,
    });
  };

  return (
    <div className="wrapper">
      <Spinner loading={loading} />
      <Timer onTimeOut={_handleTimeout} />
      <Modal
        abortTransaction={_handleAbortTransaction}
        show={modalSetting.show}
        handleClose={() => setModalSetting({ ...modalSetting, show: false })}
        title={modalSetting.title}
        description={modalSetting.description}
        buttonText={modalSetting.buttonText}
        abort={modalSetting.abort}
      />

      <CForm
        switchHash={state.switch}
        trace={state.trace}
        gateway={state.gateway}
        cbUrl={state.cbUrl}
        otpRef={formFieldsRefObj.otpRef}
        merchent={state.merchent}
        transaction={state.transaction}
        amount={state.amount}
        phoneNumber={state.phoneNumber}
        cardNumber={state.cardNumber}
        cardMonth={state.cardMonth}
        cardYear={state.cardYear}
        passwordRef={formFieldsRefObj.passwordRef}
        onUpdateState={updateStateValues}
        cardNumberRef={formFieldsRefObj.cardNumber}
        confirmCodeRef={formFieldsRefObj.confirmCodeRef}
        phoneNumberRef={formFieldsRefObj.phoneNumber}
        nationalCodeRef={formFieldsRefObj.nationalCodeRef}
        cardDateRef={formFieldsRefObj.cardDate}
        onCardInputFocus={onCardFormInputFocus}
        onCardInputBlur={onCardInputBlur}
      >
        <Card
          onUpdateState={updateStateValues}
          cardNumber={state.cardNumber}
          phoneNumber={state.phoneNumber}
          cardMonth={state.cardMonth}
          cardYear={state.cardYear}
          cardCvv={state.cardCvv}
          isCardFlipped={state.isCardFlipped}
          currentFocusedElm={currentFocusedElm}
          onCardElementClick={focusFormFieldByKey}
          cardNumberRef={cardElementsRef.cardNumber}
          phoneNumberRef={cardElementsRef.phoneNumber}
          cardDateRef={cardElementsRef.cardDate}
        />
      </CForm>
    </div>
  );
};

export default MainScreen;
