import { useTimer } from 'react-timer-hook';

function Index({ expiryTimestamp, expire, onclick, isresend }) {
  // handle the click event
  const { seconds, minutes } = useTimer({
    expiryTimestamp,
    // () => setResendCode(true)
    onExpire: expire
  });
  return (
    <>
      {/* resendCode ? setUp : null */}
      <div onClick={onclick}>
        {isresend ? (
          <span className="txt_bold">ارسال مجدد کد</span>
        ) : (
          <span className="txt_persian">
            {JSON.stringify(minutes).length === 1 ? '0' : ''}
            {minutes}:{JSON.stringify(seconds).length === 1 ? '0' : ''}
            {seconds}
          </span>
        )}
      </div>
    </>
  );
}

export default Index;
