import React, { useEffect, useState } from "react";
import { useContext } from "react";
import Context from "../../../../utils/Context";

export const Timer = ({ onTimeOut }) => {
  const [timeOut, setTimeOut] = useState(false);
  const [timeLeft, setTimeLeft] = useState({ minute: 10, second: 0 });

  const context = useContext(Context);

  const { reset, setReset } = context;

  useEffect(() => {
    if (reset) {
      setTimeLeft({ minute: 10, second: 0 });
      setReset(false);
    }
  }, [reset]);

  React.useEffect(() => {
    let myInterval = setInterval(() => {
      console.log(timeLeft);
      if (timeLeft.second > 0) {
        setTimeLeft({
          minute: timeLeft.minute,
          second: timeLeft.second - 1,
        });
      } else {
        if (timeLeft.minute === 0) {
          setTimeOut(true);
          onTimeOut();
          clearInterval(myInterval);
          setTimeLeft({
            minute: 0,
            second: 0,
          });
        } else {
          setTimeLeft({
            minute: timeLeft.minute - 1,
            second: 59,
          });
        }
      }
    }, 1000);
    return () => {
      clearInterval(myInterval);
    };
  }, [timeOut, timeLeft]);

  return <></>;
};
