import Success from "./component/pages/success/index";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { Switch } from "react-router-dom";

import { ToastContainer } from "react-toastify";
import Notfound from "./component/pages/notfound/index";
import { useState } from "react";
import LayoutSwitcher from "./LayoutSwitcher";

const Index = () => {
  return (
    <Router>
      <Switch>
        {/* <Route exact path="/" component={} /> */}
        <Route
          exact
          path="/payments/:merchent/:transaction"
          component={(props) => <LayoutSwitcher {...props} />}
        />
        <Route path="/pay-status/:status" component={Success} />

        <Route path="" component={Notfound} />
      </Switch>
      <ToastContainer rtl={true} />
    </Router>
  );
};

export default Index;
